* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  font-family: "Lato", sans-serif;
}

body {
  background-image: url("../public/img/bkg.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

.page0,
.page1,
.page2,
.page3,
.page4 {
  height: 100vh;
  margin-left: 15.625vw;
}

.pageEmbedded {
  border-radius: 100px;
  border: 1px solid;
}

.navbar {
  display: flex;
  height: 100vh;
  width: 15.625vw;
  position: fixed;
  background-color: #332f3f;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-y: scroll;
  -ms-overflow-style: none; /* IE and Edge */ /* Hide scrollbar for IE, Edge and Firefox */
  scrollbar-width: none; /* Firefox */
  /*border-right:1px solid; */
  background-image: url("../public/img/bkg.svg");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 200px;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.navbar::-webkit-scrollbar {
  display: none;
}

.navbar ul {
  list-style-type: none;
  color: white;
  font-size: 25px;
}

.navbar ul li {
  cursor: pointer;
  width: auto;
  height: 55px;
  border-radius: 35px;
  background: #e0e0e0;
  box-shadow: inset 9px 9px 18px #c1c1c1, inset -9px -9px 18px #ffffff;
  margin-bottom: 10px;
  padding-left: 10px;
  font-weight: lighter;
}

.navbar li img {
  display: inline-block;
  margin-top: 10px;
  margin-bottom: -10px;
  width: 35px;
  height: 35px;
  margin-right: 10px;
}

.navbar ul li.active,
.navbar li:hover {
  font-weight: bold;
  color: #0945b4;
  text-shadow: rgba(62, 66, 66, 0.37) -8px 0px 14px;
  -webkit-box-shadow: 0px 0px 10px 10px rgba(45, 255, 196, 0.9);
  -moz-box-shadow: 0px 0px 10px 10px rgba(45, 255, 196, 0.9);
  box-shadow: 0px 0px 10px 10px rgba(45, 255, 196, 0.9);
}

.barTech {
  display: flex;
  justify-content: center; /* align items horizontally */
  margin-left: 15.625vw;
  background-color: #332f3f;
  /*border-top: 1px solid;
  border-bottom: 1px solid;*/
  background-image: url("../public/img/bkg.svg");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 150px;
}

/*Images on top showing technologies*/
.tech {
  width: 10.42vw;
  height: 10.97vh;
}

