/*START This block is for the ipervaglioline image*/
.enlarged-image {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 9999;
}

.enlarged-image img {
  max-width: 90%;
  max-height: 90%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 100px;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  color: #fff;
  font-size: 24px;
  text-decoration: none;
}

.image-gallery a:focus + .enlarged-image {
  display: block;
  animation: fadeIn 0.5s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.image-gallery {
  position: fixed;
  top: 0;
  left: 15.625vw;
  z-index: 999;
}

.ipervaglioline {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
/*END: This block is for the ipervaglioline image*/

