@media screen and (max-width: 768px) {
 
 

 

  .navbar {
    width: 100px;
    height: 100vh;
  }

  .navbar ul li {
    height: 30px;
    font-size: 6px;
  }

  .navbar li img {
    width: 20px;
    height: 20px;
  }

  .page0,
  .page1,
  .page2,
  .page3,
  .page4 {
    margin-left: 100px;
  }

  .enlarged-image {
    height: 115vh;
    width: 115vw;
  }

  .page4,
  .page1 {
    width: 300%;
  }
  
  .image-gallery {
    position: fixed;
    top: 0;
    left: 15.625vw;
    z-index: 999;
  }
  

}
